*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    transition-property: all;
    transition-timing-function: linear;
    transition-duration: .5s;
    font-family: 'Poiret One', cursive;
    color: white;
}

:root
{
    --mainGray: #35363A;
    --gray: #6c757d;
}

body, html{
    overflow-x: hidden;
    overflow-y: hidden;
}

li{
    font-size: 30px;
}




#cardRow
{
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    background: var(--gray);
    width: 300px;
    height: 300px;
    padding: 40px 20px;
    position: absolute;
    bottom: -150px;
    border-radius: 50%;
    left: calc(50vw -150px);
    box-shadow: 0 0 30px gray;
}

h1{
    font-size: 70px;
    font-weight: lighter;
}

h3{
    font-size: 40px;
    font-weight: lighter;
}

#cardRow h1{
    margin-top: 30px;
    color: white;
    font-size: 40px;
}


.weatherCard
{
    background: rgba( 255, 255, 255, 0.25 );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    height: 350px;
    width: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.arrow  
{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    width: 80px;
    border-radius: 50%;
    bottom: 20px;
    background: var(--gray);
    box-shadow: 0 0 30px gray;
}

.arrow:hover{
    cursor: pointer;
}

.arrow i{
    color: white;
    font-size: 80px;
}

.weatherCard:hover{
    cursor: pointer;
}

.colFlex
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    min-height: 100vh;
    width: 100vw;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

#iconFix{
    margin-top: 40px;
}

#arrowRow{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 0;
    bottom: -10px;
    width: 100vw;
}

#arrowRow > div{
    margin: 0 200px;
}



#iconFix i{
    color: white;
    font-size: 100px;
}


.columnFlex{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

button{
    border: none;
    padding: 10px 30px;
    border-radius: 16px;
    background: white;
    color: black;
    font-size: 25px;
    margin: 20px;
    box-shadow: 0 0 10px gray;
}

button:hover{
    box-shadow: 0 0 30px gray;
    cursor: pointer;
}

#hourlyTable
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 700px;
    padding: 20px;
}

.tableRow{
    display: flex;
    width: 100%;
    justify-content: flex-start;
    align-items: center;
}


.col2{
    margin-left: auto;
    display: flex;
}

#wholeTable{
    display: flex;
    justify-content: center;
    align-items: center;
}
.hoverable{
    transition-duration: .2s;
}
.hoverable:hover{
    transform: scale(120%);
    cursor: pointer;
}

.rowFlex{
    display: flex;
    justify-content: center;
    align-items: center;
}

.subCol{
    margin: 10px;
}

.search {
    border: 1px solid grey;
    border-radius: 5px;
    height: 50px;
    padding: 8px;
    outline: 0;
    background-color: #f5f5f5;
    color: black;
    font-size: 25px;
    width: 350px;
  }
  
  .search-icon {
    position: absolute;
    top: 12px;
    left: 8px;
    width: 25px;
    z-index: 1;
  }
  
  .clear-icon {
    position: absolute;
    top: 9px;
    right: 8px;
    width: 12px;
    cursor: pointer;
    visibility: hidden;
    z-index: 1;
  }
  
  .search:hover, .search:focus {
    border: 1px solid #009688;
    background-color: white;
  }


option{
    color: black;
}

select{
    color: black;
    width: 350px;
    font-size: 25px;
    border-radius: 5px;
    height: 50px;
}

.wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
